.About {
  overflow: hidden;
  display: flex;
  width: 100%;
  height: calc(100vh - 64px);
  font-family: "PeaceR";
}
.Aleft {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  .naeun {
    width: 460px;
  }
  .Aphoto {
    position: relative;
    width: 470px;
  }
  .Aphoto:hover .naeun1 {
    display: block;
  }
  .Aleft1 {
    margin: 40px 0 0 0;
    height: 100px;
    font-size: 1.5em;
  }
  .Aleft1::before {
    content: "ðŸ‘†";
    position: absolute;
    font-size: 15px;
    left: 48%;
    top: 59%;
  }
}
.naeun1 {
  display: none;
  position: absolute;
  width: 250px;
  border-radius: 190px;
  top: 50px;
  right: 105px;
  box-shadow: white 0px 0px 0px 2px, pink 4px 4px 6px -1px,
    blue 0px 1px 0px inset;
}

.Aright {
  width: 50%;
  position: relative;
  height: 670px;

  .Abar {
    width: 10px;
    height: 550px;
    position: absolute;
    top: 40px;
    left: 40px;
    border-left: 10px solid pink;
  }

  .Aword {
    padding-left: 110px;
    padding-top: 100px;
    font-size: 20px;
  }
  .Aword::first-line {
    font-size: 25px;
    font-weight: bold;
  }
  .Aword::before {
    content: "";
    position: absolute;
    width: 17px;
    height: 17px;
    border: 5px solid pink;
    border-radius: 100%;
    background-color: snow;
    left: 30px;
  }

  .Asesac {
    color: yellowgreen;
  }
  .Aimage {
    color: rgb(102, 102, 102);
  }
  .AJoara {
    color: #2a54ae;
  }
}

@media (max-width: 820px) {
  .Aword {
    width: 200px;
  }
}

@media (max-width: 700px) {
  .Aleft1 {
    width: 240px;
  }
  p {
    font-size: 14px;
    margin-top: 2px;
  }
}
