.Plan {
  width: 100%;
  height: calc(100vh - 64px);
  font-family: "PeaceR";
}

.PlOne {
  height: 330px;
  margin: 20px;

  span {
    font-weight: bold;
  }
}

.PlOne-main,
.PlOne-sub {
  height: 40%;
  margin: 0 15px;
  .PlOne-logo {
    display: flex;
    margin: 10px 0;

    img {
      margin-right: 5px;
    }
  }
}

.PlTwo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;

  .joOne {
    font-size: 25px;
    font-weight: bold;
    display: flex;
    overflow: hidden;

    div {
      margin-left: 10px;
      color: white;
      padding: 1px;

      height: 30px;
      margin-bottom: 30px;
    }

    .joOne-fir {
      background-color: #dc143d;
      animation: show 0.5s linear;
    }
    .joOne-sec {
      background-color: #4ec7f3;
      animation: show 0.5s linear both;
      animation-delay: 0.5s;
    }
    .joOne-thi {
      background-color: #42c58a;
      animation: show 0.5s linear both;
      animation-delay: 1s;
    }

    @keyframes show {
      0% {
        margin-top: -40px;
      }
      100% {
        margin-top: 0px;
      }
    }
  }

  .Plword-height {
    height: 70px;
  }

  .joTwo {
    height: 240px;
    margin: 0 25px 0 25px;
    font-size: 18px;
    line-height: 28px;
    // position: fixed;

    .Sone {
      border-bottom: 2px solid #f26728;
    }
    .Stwo {
      border-bottom: 2px solid #2dabdd;
    }
    .SThree {
      border-bottom: 2px solid #f2c120;
    }
  }
}

@media (min-width: 1117px) {
  .PlOne {
    margin: 20px 100px;
  }
}

@media (max-width: 522px) {
  .PlOne {
    img {
      width: 18%;
    }
  }
}

@media (max-width: 463px) {
  .PlOne {
    font-size: 14px;
    height: 300px;
  }
  .PlOne-main,
  .PlOne-sub {
    height: 35%;
  }
  .PlTwo .joTwo {
    margin: 0 25px;
  }
}

@media (max-width: 400px) {
  .PlTwo .joOne {
    font-size: 20px;
  }
}

@media (max-width: 379px) {
  .PlOne-main,
  .PlOne-sub {
    height: 40%;
  }
}

@media (max-width: 329px) {
  .PlTwo .joOne {
    font-size: 18px;
  }
  .PlTwo .Plword-height {
    height: 60px;
  }
  .PlTwo .joTwo {
    font-size: 16px;
  }
}
